<template>
  <v-card class="pa-2" @dblclick="update">
    <v-card-title><v-btn text @click="changeMode">{{titles[mode]}}</v-btn></v-card-title>
    <v-card-text v-if="chartdata">
      <LineChart v-if="mode === 'day'" :chartdata="chartdata.day" :options="options" :styles="{ height: 200, position: 'relative' }"/>
      <LineChart v-else :chartdata="chartdata.heat" :options="options" :styles="{ height: 200, position: 'relative' }"/>
    </v-card-text>
  </v-card>
</template>

<script>
import LineChart from '@/render/components/Common/charts/LineChart.vue';

export default {
  components: { LineChart },
  data: () => ({
    chartsource: null,
    titles: {
      day: 'Тикеты',
      heat: 'Жара'
    },
    mode: 'day',
    options: {
      responsive: true,
      maintainAspectRatio: false,
      elements: {
        point: {
          radius: 0
        }
      },
      scales: {
        yAxes: [{
          ticks: {
            beginAtZero: true
          }
        }]
      }
    }
  }),

  mounted() {
    this.update();
  },

  methods: {
    async update() {
      this.chartsource = await this.tkb.query('get', 'stats');
    },

    changeMode() {
      this.mode = this.mode === 'day' ? 'heat' : 'day';
    }
  },

  computed: {
    chartdata() {
      if (!this.chartsource) {
        return;
      }

      return {
        day: {
          labels: this.chartsource.data.map((_, i) => `${Math.floor((i + 1) / 60)}:00`),
          datasets: [{
            data: this.chartsource.data,
            borderColor: this.$vuetify.theme.parsedTheme.accent.lighten2,
            backgroundColor: this.$vuetify.theme.parsedTheme.accent.lighten1,
            fill: true,
            label: 'Сегодня'
          }]
        },
        heat: {
          labels: this.chartsource.heat.map((_, i) => `${Math.floor((i + 1) / 60 * 5)}:00`),
          datasets: [{
            data: this.chartsource.heat,
            borderColor: 'rgb(250, 25, 25)',
            backgroundColor: 'rgba(250, 25, 25, 0.5)',
            tension: 0.4,
            fill: true,
            label: 'Температура'
          }]
        }
      };
    }
  }
};
</script>
