<template>
  <v-card class="pa-2" v-if="shift">
    <v-card-title>Ваша смена</v-card-title>
    <v-card-text>
      <div>Смена началась {{time}}</div>
      <div>Отправлено {{shift.messages}} сообщений.</div>
    </v-card-text>
  </v-card>
</template>

<script>
import moment from 'moment';

moment.locale('ru');

export default {
  computed: {
    time () {
      return moment(this.shift.startAt * 1000).fromNow();
    }
  },
  asyncComputed: {
    shift () {
      return this.tkb.query('get', 'staff/shift');
    }
  }
}
</script>