<template>
  <v-card class="pa-2" @dblclick="update">
    <v-card-title><v-btn text @click="changeMode">{{titles[mode]}}</v-btn></v-card-title>
    <v-card-text v-if="votes">
      <BarChart v-if="mode === 'voterat'" :style="{ position: 'relative', height: 100 }" :chartdata="chartdata" :options="options"/>
      <v-list v-else style="max-height: 400px; overflow-y: auto">
        <v-list-item two-line v-for="item in myVotes" :key="item.id" @click="() => tabMgr.open('Ticket', { ticketId: item.ticketId })">
          <v-list-item-avatar style="font-size: 30px">{{['🤬', '😡', '😠', '🙁', '😐', '🤔', '🙂', '☺️', '😏', '🤩'][item.vote - 1]}}</v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>{{item.client}} #{{item.ticketId}}</v-list-item-title>
            <v-list-item-subtitle>{{item.note}}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-card-text>
  </v-card>
</template>

<script>
import BarChart from '@/render/components/Common/charts/BarChart.vue';

export default {
  components: { BarChart },
  data: () => ({
    votes: null,
    titles: {
      voterat: 'Рейтинг оценок',
      votes: 'Мои оценки'
    },
    mode: 'voterat',
    options: {}
  }),

  mounted () {
    this.update();
  },

  methods: {
    async update () {
      this.votes = await this.tkb.query('get', 'vote');
    },

    changeMode () {
      this.mode = this.mode === 'voterat' ? 'votes' : 'voterat';
    },

    computeRating(votes) {
      return votes.reduce((acc, v) => v.vote - 5 + acc, 0);
    }
  },

  computed: {
    chartdata () {
      if (!this.votes) {
        return;
      }

      const staff = [...new Set(this.votes.map(v => v.responsible))];
      return {
        labels: staff,
        datasets: [{
          label: 'Рейтинг за неделю',
          data: staff.map(v => this.computeRating(this.votes.filter(b => b.responsible === v))),
          backgroundColor: this.$vuetify.theme.parsedTheme.accent.lighten1
        }]
      };
    },

    myVotes() {
      if (!this.votes) {
        return;
      }

      return this.votes.filter(v => v.responsible === this.api.userInfo.name);
    }
  }
}
</script>